<template>
  <div class="bg-white shadow rounded-lg p-6">
    <div class="flex items-center justify-between mb-4">
      <h3 class="text-lg font-medium text-gray-900">Package Usage Monitor</h3>
      <button 
        @click="refreshUsage"
        class="inline-flex items-center px-3 py-2 border border-gray-300 shadow-sm text-sm leading-4 font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
        :disabled="loading"
      >
        <font-awesome-icon 
          :icon="['fas', 'refresh']" 
          :class="['w-4 h-4 mr-2', loading ? 'animate-spin' : '']" 
        />
        
      </button>
    </div>

    <!-- Package Info -->
    <div v-if="collection && collection.package" class="mb-6">
      <div class="flex items-center space-x-4 p-4 bg-blue-50 rounded-lg">
        <div class="flex-shrink-0">
          <font-awesome-icon :icon="['fas', 'gift']" class="w-8 h-8 text-blue-600" />
        </div>
        <div class="flex-1">
          <h4 class="text-lg font-semibold text-blue-900">{{ collection.package.name }}</h4>
          <p class="text-sm text-blue-700">{{ collection.package.description }}</p>
          <div class="mt-2 flex items-center space-x-4 text-sm text-blue-600">
            <span>{{ collection.package.price }}/{{ collection.package.duration }} </span>
            <span :class="[
              'inline-flex px-2 py-1 rounded-full text-xs font-medium',
              collection.package.status ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
            ]">
              {{ collection.package.status ? '' : '' }}
            </span>
          </div>
        </div>
      </div>
    </div>

    <!-- Usage Metrics -->
    <div v-if="usage" class="space-y-6">
      <!-- Assets Usage -->
      <div class="usage-metric">
        <div class="flex items-center justify-between mb-2">
          <div class="flex items-center">
            <font-awesome-icon :icon="['fas', 'database']" class="w-5 h-5 text-indigo-600 mr-2" />
            <span class="text-sm font-medium text-gray-900">Assets</span>
          </div>
          <span class="text-sm text-gray-600">{{ formatLimits.assets.usage }}</span>
        </div>
        <div class="w-full bg-gray-200 rounded-full h-2">
          <div 
            class="h-2 rounded-full transition-all duration-300"
            :class="getProgressColor(formatLimits.assets.percentage)"
            :style="{ width: `${Math.min(formatLimits.assets.percentage, 100)}%` }"
          ></div>
        </div>
        <div v-if="formatLimits.assets.percentage >= 80" class="mt-1 text-xs text-orange-600">
           
        </div>
      </div>

      <!-- Users Usage -->
      <div class="usage-metric">
        <div class="flex items-center justify-between mb-2">
          <div class="flex items-center">
            <font-awesome-icon :icon="['fas', 'users']" class="w-5 h-5 text-indigo-600 mr-2" />
            <span class="text-sm font-medium text-gray-900">Users</span>
          </div>
          <span class="text-sm text-gray-600">{{ formatLimits.users.usage }}</span>
        </div>
        <div class="w-full bg-gray-200 rounded-full h-2">
          <div 
            class="h-2 rounded-full transition-all duration-300"
            :class="getProgressColor(formatLimits.users.percentage)"
            :style="{ width: `${Math.min(formatLimits.users.percentage, 100)}%` }"
          ></div>
        </div>
        <div v-if="formatLimits.users.percentage >= 80" class="mt-1 text-xs text-orange-600">
           
        </div>
      </div>

      <!-- Storage Usage -->
      <div class="usage-metric">
        <div class="flex items-center justify-between mb-2">
          <div class="flex items-center">
            <font-awesome-icon :icon="['fas', 'hdd']" class="w-5 h-5 text-indigo-600 mr-2" />
            <span class="text-sm font-medium text-gray-900">Storage</span>
          </div>
          <span class="text-sm text-gray-600">{{ formatLimits.storage.usage }}</span>
        </div>
        <div class="w-full bg-gray-200 rounded-full h-2">
          <div 
            class="h-2 rounded-full transition-all duration-300"
            :class="getProgressColor(formatLimits.storage.percentage)"
            :style="{ width: `${Math.min(formatLimits.storage.percentage, 100)}%` }"
          ></div>
        </div>
        <div v-if="formatLimits.storage.percentage >= 80" class="mt-1 text-xs text-orange-600">
           
        </div>
      </div>

      <!-- Bandwidth Usage -->
      <div class="usage-metric">
        <div class="flex items-center justify-between mb-2">
          <div class="flex items-center">
            <font-awesome-icon :icon="['fas', 'tachometer-alt']" class="w-5 h-5 text-indigo-600 mr-2" />
            <span class="text-sm font-medium text-gray-900">Bandwidth</span>
          </div>
          <span class="text-sm text-gray-600">{{ formatLimits.bandwidth.usage }}</span>
        </div>
        <div class="w-full bg-gray-200 rounded-full h-2">
          <div 
            class="h-2 rounded-full transition-all duration-300"
            :class="getProgressColor(formatLimits.bandwidth.percentage)"
            :style="{ width: `${Math.min(formatLimits.bandwidth.percentage, 100)}%` }"
          ></div>
        </div>
        <div v-if="formatLimits.bandwidth.percentage >= 80" class="mt-1 text-xs text-orange-600">
           
        </div>
      </div>
    </div>

    <!-- Features Status -->
    <div v-if="collection && collection.package" class="mt-6 border-t pt-6">
      <h4 class="text-sm font-medium text-gray-900 mb-3"> Package</h4>
      <div class="grid grid-cols-2 gap-3">
        <div v-for="(feature, key) in packageFeatures" :key="key" class="flex items-center">
          <font-awesome-icon 
            :icon="['fas', feature.available ? 'check-circle' : 'times-circle']" 
            :class="[
              'w-4 h-4 mr-2',
              feature.available ? 'text-green-500' : 'text-red-500'
            ]" 
          />
          <span class="text-sm" :class="feature.available ? 'text-gray-900' : 'text-gray-500'">
            {{ feature.label }}
          </span>
        </div>
      </div>
    </div>

    <!-- Actions -->
    <div class="mt-6 border-t pt-6 flex space-x-3">
      <button 
        @click="$emit('upgrade-package')"
        class="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
        v-if="hasLimitWarnings"
      >
        <font-awesome-icon :icon="['fas', 'arrow-up']" class="w-4 h-4 mr-2" />
         Package
      </button>
      
      <button 
        @click="$emit('view-packages')"
        class="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
      >
        <font-awesome-icon :icon="['fas', 'gift']" class="w-4 h-4 mr-2" />
         Package 
      </button>
    </div>
  </div>
</template>

<script>
import { 
  validateAllLimits, 
  formatPackageLimits, 
  checkPackageFeature 
} from './packageValidation.js';

export default {
  name: 'PackageLimitMonitor',
  props: {
    collection: {
      type: Object,
      required: true
    },
    usage: {
      type: Object,
      default: () => ({
        assets: 0,
        users: 0,
        storageGB: 0,
        bandwidthGB: 0
      })
    }
  },
  emits: ['upgrade-package', 'view-packages', 'refresh-usage'],
  data() {
    return {
      loading: false,
      validationResult: null
    };
  },
  computed: {
    formatLimits() {
      if (!this.collection || !this.collection.package) {
        return {
          assets: { usage: '0 / ', percentage: 0 },
          users: { usage: '0 / ', percentage: 0 },
          storage: { usage: '0 GB / ', percentage: 0 },
          bandwidth: { usage: '0 GB / ', percentage: 0 }
        };
      }
      
      return formatPackageLimits(this.collection.package.limits, this.usage);
    },
    packageFeatures() {
      if (!this.collection || !this.collection.package) return {};
      
      const features = {
        customDomain: { label: 'Custom Domain', available: false },
        sslCertificate: { label: 'SSL Certificate', available: false },
        apiAccess: { label: 'API Access', available: false },
        prioritySupport: { label: 'Priority Support', available: false }
      };
      
      Object.keys(features).forEach(key => {
        const result = checkPackageFeature(this.collection, key);
        features[key].available = result.available;
      });
      
      return features;
    },
    hasLimitWarnings() {
      return Object.values(this.formatLimits).some(limit => limit.percentage >= 80);
    }
  },
  methods: {
    getProgressColor(percentage) {
      if (percentage >= 90) return 'bg-red-500';
      if (percentage >= 80) return 'bg-orange-500';
      if (percentage >= 60) return 'bg-yellow-500';
      return 'bg-green-500';
    },
    async refreshUsage() {
      this.loading = true;
      try {
        this.$emit('refresh-usage');
        // Simulate API call delay
        await new Promise(resolve => setTimeout(resolve, 1000));
      } finally {
        this.loading = false;
      }
    },
    validateLimits(additional = {}) {
      if (!this.collection || !this.collection.package) return null;
      
      this.validationResult = validateAllLimits(this.collection, this.usage, additional);
      return this.validationResult;
    }
  },
  watch: {
    collection: {
      handler() {
        this.validateLimits();
      },
      immediate: true
    },
    usage: {
      handler() {
        this.validateLimits();
      },
      deep: true,
      immediate: true
    }
  }
};
</script>

<style scoped>
.usage-metric {
  padding: 1rem;
  background-color: #f9fafb;
  border-radius: 0.5rem;
}

.animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>
