<template>
  <div class="bg-white shadow rounded-lg p-6">
    <div class="flex items-center justify-between mb-6">
      <h3 class="text-lg font-medium text-gray-900">Package Subscription</h3>
      <button v-if="!currentSubscription" 
              @click="showAssignmentModal = true"
              class="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700">
        <font-awesome-icon :icon="['fas', 'gift']" class="mr-2" />
         Package
      </button>
    </div>

    <!-- Current Subscription -->
    <div v-if="currentSubscription" class="space-y-6">
      <!-- Package Info -->
      <div class="p-4 bg-gradient-to-r from-indigo-50 to-blue-50 rounded-lg border border-indigo-200">
        <div class="flex items-center justify-between">
          <div class="flex items-center">
            <div class="flex-shrink-0">
              <font-awesome-icon :icon="['fas', 'gift']" class="w-8 h-8 text-indigo-600" />
            </div>
            <div class="ml-4">
              <h4 class="text-lg font-semibold text-indigo-900">{{ currentPackage?.name }}</h4>
              <p class="text-sm text-indigo-700">{{ currentPackage?.description }}</p>
            </div>
          </div>
          <div class="text-right">
            <span :class="[
              'inline-flex px-3 py-1 rounded-full text-sm font-medium',
              getStatusClass(currentSubscription.status)
            ]">
              {{ getStatusText(currentSubscription.status) }}
            </span>
          </div>
        </div>
      </div>

      <!-- Subscription Details -->
      <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
        <!-- Billing Info -->
        <div class="space-y-4">
          <h5 class="text-sm font-medium text-gray-900"></h5>
          
          <div class="space-y-3">
            <div class="flex justify-between">
              <span class="text-sm text-gray-600">:</span>
              <span class="text-sm font-medium text-gray-900">{{ getBillingCycleLabel() }}</span>
            </div>
            
            <div class="flex justify-between">
              <span class="text-sm text-gray-600">:</span>
              <span class="text-sm font-medium text-gray-900">{{ currentSubscription.amount?.toLocaleString() }}</span>
            </div>
            
            <div class="flex justify-between">
              <span class="text-sm text-gray-600">:</span>
              <span class="text-sm font-medium text-gray-900">{{ formatDate(currentSubscription.startDate) }}</span>
            </div>
            
            <div class="flex justify-between">
              <span class="text-sm text-gray-600">:</span>
              <span class="text-sm font-medium text-gray-900">{{ formatDate(currentSubscription.endDate) }}</span>
            </div>
            
            <div class="flex justify-between">
              <span class="text-sm text-gray-600">:</span>
              <span class="text-sm font-medium text-gray-900">
                {{ currentSubscription.autoRenewal ? '' : '' }}
              </span>
            </div>
            
            <div v-if="currentSubscription.trialDays > 0" class="flex justify-between">
              <span class="text-sm text-gray-600">:</span>
              <span class="text-sm font-medium text-green-600">{{ currentSubscription.trialDays }} </span>
            </div>
          </div>
        </div>

        <!-- Usage Info -->
        <div class="space-y-4">
          <h5 class="text-sm font-medium text-gray-900"></h5>
          
          <!-- Usage Progress -->
          <div class="space-y-3">
            <div>
              <div class="flex justify-between text-sm mb-1">
                <span class="text-gray-600">Assets</span>
                <span class="font-medium">{{ usage.assets }}/{{ currentPackage?.limits.maxAssets || '' }}</span>
              </div>
              <div class="w-full bg-gray-200 rounded-full h-2">
                <div class="bg-indigo-600 h-2 rounded-full" 
                     :style="{ width: getUsagePercentage('assets') + '%' }"></div>
              </div>
            </div>
            
            <div>
              <div class="flex justify-between text-sm mb-1">
                <span class="text-gray-600">Users</span>
                <span class="font-medium">{{ usage.users }}/{{ currentPackage?.limits.maxUsers || '' }}</span>
              </div>
              <div class="w-full bg-gray-200 rounded-full h-2">
                <div class="bg-indigo-600 h-2 rounded-full" 
                     :style="{ width: getUsagePercentage('users') + '%' }"></div>
              </div>
            </div>
            
            <div>
              <div class="flex justify-between text-sm mb-1">
                <span class="text-gray-600">Storage</span>
                <span class="font-medium">{{ usage.storageGB?.toFixed(2) }}GB/{{ currentPackage?.limits.storageGB || '' }}GB</span>
              </div>
              <div class="w-full bg-gray-200 rounded-full h-2">
                <div class="bg-indigo-600 h-2 rounded-full" 
                     :style="{ width: getUsagePercentage('storage') + '%' }"></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Days Remaining -->
      <div v-if="daysRemaining !== null" class="p-4 rounded-lg" 
           :class="[
             daysRemaining <= 7 ? 'bg-red-50 border border-red-200' : 
             daysRemaining <= 30 ? 'bg-yellow-50 border border-yellow-200' : 
             'bg-green-50 border border-green-200'
           ]">
        <div class="flex items-center">
          <font-awesome-icon 
            :icon="['fas', daysRemaining <= 7 ? 'exclamation-triangle' : 'calendar-alt']" 
            :class="[
              'w-5 h-5 mr-3',
              daysRemaining <= 7 ? 'text-red-500' : 
              daysRemaining <= 30 ? 'text-yellow-500' : 
              'text-green-500'
            ]" />
          <div>
            <p class="text-sm font-medium" 
               :class="[
                 daysRemaining <= 7 ? 'text-red-800' : 
                 daysRemaining <= 30 ? 'text-yellow-800' : 
                 'text-green-800'
               ]">
              {{ getExpiryText }}
            </p>
            <p class="text-sm" 
               :class="[
                 daysRemaining <= 7 ? 'text-red-700' : 
                 daysRemaining <= 30 ? 'text-yellow-700' : 
                 'text-green-700'
               ]">
              {{ currentSubscription.autoRenewal ? '' : '' }}
            </p>
          </div>
        </div>
      </div>

      <!-- Action Buttons -->
      <div class="flex space-x-3">
        <button @click="showUpgradeModal = true"
                class="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-600 hover:bg-green-700">
          <font-awesome-icon :icon="['fas', 'arrow-up']" class="mr-2" />
           Package
        </button>
        
        <button @click="toggleAutoRenewal"
                :class="[
                  'inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm',
                  currentSubscription.autoRenewal 
                    ? 'text-red-700 bg-red-100 hover:bg-red-200' 
                    : 'text-green-700 bg-green-100 hover:bg-green-200'
                ]">
          <font-awesome-icon :icon="['fas', currentSubscription.autoRenewal ? 'pause' : 'play']" class="mr-2" />
          {{ currentSubscription.autoRenewal ? '' : '' }}
        </button>
        
        <button @click="showBillingHistory = true"
                class="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50">
          <font-awesome-icon :icon="['fas', 'receipt']" class="mr-2" />
          
        </button>
        
        <button @click="showAdvancedConfig = true"
                class="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-purple-600 hover:bg-purple-700">
          <font-awesome-icon :icon="['fas', 'cogs']" class="mr-2" />
          
        </button>
        
        <button @click="cancelSubscription"
                class="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-red-700 bg-white hover:bg-red-50">
          <font-awesome-icon :icon="['fas', 'times']" class="mr-2" />
           Package
        </button>
      </div>
    </div>

    <!-- No Subscription -->
    <div v-else class="text-center py-12">
      <font-awesome-icon :icon="['fas', 'gift']" class="w-16 h-16 text-gray-400 mb-4" />
      <h3 class="text-lg font-medium text-gray-900 mb-2"> Package</h3>
      <p class="text-gray-600 mb-6"> Package </p>
      <button @click="showAssignmentModal = true"
              class="inline-flex items-center px-6 py-3 border border-transparent text-base font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700">
        <font-awesome-icon :icon="['fas', 'gift']" class="mr-2" />
         Package
      </button>
    </div>

    <!-- Package Assignment Modal -->
    <PackageAssignmentModal 
      :isOpen="showAssignmentModal"
      :collection="collection"
      :hostkey="hostkey"
      @close="showAssignmentModal = false"
      @assigned="handlePackageAssigned"
    />

    <!-- Billing History Modal -->
    <PackageBillingHistory 
      v-if="showBillingHistory"
      :collection="collection"
      :hostkey="hostkey"
      @close="showBillingHistory = false"
    />

    <!-- Advanced Configuration Modal -->
    <div v-if="showAdvancedConfig" class="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
      <div class="relative top-10 mx-auto p-5 border w-11/12 md:w-5/6 lg:w-4/5 shadow-lg rounded-md bg-white max-h-screen overflow-y-auto">
        <AdvancedPackageConfig 
          :collection="collection"
          :hostkey="hostkey"
          @close="showAdvancedConfig = false"
          @configured="handleAdvancedConfigured"
        />
      </div>
    </div>
  </div>
</template>

<script>
import PackageAssignmentModal from './PackageAssignmentModal.vue';
import PackageBillingHistory from './PackageBillingHistory.vue';
import AdvancedPackageConfig from './AdvancedPackageConfig.vue';

export default {
  name: 'PackageSubscriptionManager',
  components: {
    PackageAssignmentModal,
    PackageBillingHistory,
    AdvancedPackageConfig
  },
  props: {
    collection: {
      type: Object,
      required: true
    },
    hostkey: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      currentSubscription: null,
      currentPackage: null,
      usage: {
        assets: 0,
        users: 0,
        storageGB: 0,
        bandwidthGB: 0
      },
      billingHistory: [],
      showAssignmentModal: false,
      showUpgradeModal: false,
      showBillingHistory: false,
      showAdvancedConfig: false,
      loading: false
    };
  },
  computed: {
    daysRemaining() {
      if (!this.currentSubscription?.endDate) return null;
      
      const endDate = new Date(this.currentSubscription.endDate);
      const today = new Date();
      const diffTime = endDate - today;
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      
      return diffDays;
    },
    getExpiryText() {
      if (this.daysRemaining === null) return '';
      if (this.daysRemaining <= 0) return 'Package ';
      return ' ' + this.daysRemaining + ' ';
    }
  },
  methods: {
    async loadSubscriptionData() {
      try {
        this.loading = true;
        
        // Load current subscription
        const subResponse = await fetch('https://gateway.cloudrestfulapi.com/api/package-assignment/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'findOne',
            args: [{
              collectionId: this.collection._id,
              status: { $in: ['active', 'trial'] }
            }]
          })
        });

        if (subResponse.ok) {
          this.currentSubscription = await subResponse.json();
          
          if (this.currentSubscription) {
            // Load package details
            await this.loadPackageDetails();
            
            // Load usage data
            await this.loadUsageData();
            
            // Load billing history
            await this.loadBillingHistory();
          }
        }
        
      } catch (error) {
        console.error('Error loading subscription data:', error);
      } finally {
        this.loading = false;
      }
    },
    async loadPackageDetails() {
      try {
        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/package/${this.currentSubscription.packageId}`, {
          headers: {
            'client-token-key': this.hostkey
          }
        });

        if (response.ok) {
          this.currentPackage = await response.json();
        }
      } catch (error) {
        console.error('Error loading package details:', error);
      }
    },
    async loadUsageData() {
      try {
        // Load collection assets count
        const assetsResponse = await fetch('https://gateway.cloudrestfulapi.com/api/hostname/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'find',
            args: [{ parent: this.collection._id, siteType: 'asset' }]
          })
        });

        if (assetsResponse.ok) {
          const assets = await assetsResponse.json();
          this.usage.assets = assets.length;
        }

        // Load users count
        const usersResponse = await fetch('https://gateway.cloudrestfulapi.com/api/user/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'find',
            args: [{ collection: { $in: [this.collection._id] } }]
          })
        });

        if (usersResponse.ok) {
          const users = await usersResponse.json();
          this.usage.users = users.length;
        }

        // Load storage usage (simplified)
        this.usage.storageGB = Math.random() * 10; // Mock data
        this.usage.bandwidthGB = Math.random() * 50; // Mock data

      } catch (error) {
        console.error('Error loading usage data:', error);
      }
    },
    async loadBillingHistory() {
      try {
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/billing-history/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'find',
            args: [{ collectionId: this.collection._id }],
            sort: { billingDate: -1 }
          })
        });

        if (response.ok) {
          this.billingHistory = await response.json();
        }
      } catch (error) {
        console.error('Error loading billing history:', error);
        this.billingHistory = [];
      }
    },
    getBillingCycleLabel() {
      if (!this.currentPackage || !this.currentSubscription) return '';
      
      const cycle = this.currentPackage.pricing.billingCycles.find(
        c => c.type === this.currentSubscription.billingCycle
      );
      
      return cycle?.label || this.currentSubscription.billingCycle;
    },
    getStatusClass(status) {
      const classes = {
        'active': 'bg-green-100 text-green-800',
        'trial': 'bg-blue-100 text-blue-800',
        'expired': 'bg-red-100 text-red-800',
        'cancelled': 'bg-gray-100 text-gray-800'
      };
      return classes[status] || 'bg-gray-100 text-gray-800';
    },
    getStatusText(status) {
      const texts = {
        'active': '',
        'trial': '',
        'expired': '',
        'cancelled': ''
      };
      return texts[status] || status;
    },
    getUsagePercentage(type) {
      if (!this.currentPackage) return 0;
      
      const limits = this.currentPackage.limits;
      const current = this.usage[type];
      
      let limit;
      switch (type) {
        case 'assets':
          limit = limits.maxAssets;
          break;
        case 'users':
          limit = limits.maxUsers;
          break;
        case 'storage':
          limit = limits.storageGB;
          break;
        default:
          return 0;
      }
      
      if (!limit) return 0;
      return Math.min((current / limit) * 100, 100);
    },
    formatDate(dateString) {
      if (!dateString) return '';
      return new Date(dateString).toLocaleDateString('th-TH');
    },
    async toggleAutoRenewal() {
      try {
        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/package-assignment/${this.currentSubscription._id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            data: {
              autoRenewal: !this.currentSubscription.autoRenewal,
              updatedAt: new Date()
            }
          })
        });

        if (response.ok) {
          this.currentSubscription.autoRenewal = !this.currentSubscription.autoRenewal;
        }
      } catch (error) {
        console.error('Error toggling auto renewal:', error);
      }
    },
    async cancelSubscription() {
      if (!confirm(' Package ?')) return;
      
      try {
        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/package-assignment/${this.currentSubscription._id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            data: {
              status: 'cancelled',
              cancelledAt: new Date(),
              updatedAt: new Date()
            }
          })
        });

        if (response.ok) {
          this.currentSubscription.status = 'cancelled';
        }
      } catch (error) {
        console.error('Error cancelling subscription:', error);
      }
    },
    handlePackageAssigned() {
      this.loadSubscriptionData();
    },
    handleAdvancedConfigured(configuration) {
      console.log('Advanced configuration received:', configuration);
      this.showAdvancedConfig = false;
      this.loadSubscriptionData();
    }
  },
  async mounted() {
    await this.loadSubscriptionData();
  }
};
</script>
