<script>
export default {
  name: 'PaymentProcessor',
  props: {
    invoice: {
      type: Object,
      required: true
    },
    amount: {
      type: Number,
      required: true
    }
  },
  emits: ['close', 'success', 'error'],
  data() {
    return {
      selectedMethod: '',
      processing: false,
      cardData: {
        number: '',
        expiry: '',
        cvv: '',
        name: ''
      },
      receiptFile: null
    };
  },
  computed: {
    isFormValid() {
      if (!this.selectedMethod) return false;
      
      switch (this.selectedMethod) {
        case 'credit_card':
          return this.cardData.number.length >= 16 && 
                 this.cardData.expiry.length === 5 && 
                 this.cardData.cvv.length >= 3 && 
                 this.cardData.name.trim();
        case 'bank_transfer':
          return this.receiptFile !== null;
        case 'qr_payment':
        case 'promptpay':
          return true; // QR payments are considered valid once method is selected
        default:
          return false;
      }
    }
  },
  methods: {
    formatCardNumber(event) {
      let value = event.target.value.replace(/\s/g, '').replace(/[^0-9]/gi, '');
      let formattedValue = value.match(/.{1,4}/g)?.join(' ') || value;
      this.cardData.number = formattedValue;
    },
    formatExpiry(event) {
      let value = event.target.value.replace(/\D/g, '');
      if (value.length >= 2) {
        value = value.substring(0, 2) + '/' + value.substring(2, 4);
      }
      this.cardData.expiry = value;
    },
    handleReceiptUpload(event) {
      const file = event.target.files[0];
      if (file) {
        // Validate file size (max 5MB)
        if (file.size > 5 * 1024 * 1024) {
          alert('  5MB');
          return;
        }
        
        // Validate file type
        const allowedTypes = ['image/jpeg', 'image/png', 'image/gif', 'application/pdf'];
        if (!allowedTypes.includes(file.type)) {
          alert(' PDF ');
          return;
        }
        
        this.receiptFile = file;
      }
    },
    async processPayment() {
      this.processing = true;
      
      try {
        let paymentData = {
          invoiceId: this.invoice._id,
          amount: this.amount,
          method: this.selectedMethod,
          timestamp: new Date().toISOString()
        };
        
        switch (this.selectedMethod) {
          case 'credit_card':
            paymentData = {
              ...paymentData,
              card: {
                number: this.cardData.number.replace(/\s/g, ''),
                expiry: this.cardData.expiry,
                cvv: this.cardData.cvv,
                name: this.cardData.name
              }
            };
            await this.processCreditCardPayment(paymentData);
            break;
            
          case 'bank_transfer':
            if (this.receiptFile) {
              const receiptUrl = await this.uploadReceipt(this.receiptFile);
              paymentData.receiptUrl = receiptUrl;
            }
            await this.processBankTransfer(paymentData);
            break;
            
          case 'qr_payment':
          case 'promptpay':
            await this.processQRPayment(paymentData);
            break;
        }
        
      } catch (error) {
        console.error('Payment processing error:', error);
        this.$emit('error', error);
      } finally {
        this.processing = false;
      }
    },
    async processCreditCardPayment(paymentData) {
      // Simulate credit card processing
      await this.simulatePaymentDelay();
      
      // In real implementation, integrate with payment gateway like Stripe, Omise, etc.
      const success = Math.random() > 0.1; // 90% success rate for demo
      
      if (success) {
        this.$emit('success', {
          ...paymentData,
          transactionId: this.generateTransactionId(),
          status: 'completed'
        });
      } else {
        throw new Error(' ');
      }
    },
    async processBankTransfer(paymentData) {
      // Bank transfer typically requires manual verification
      await this.simulatePaymentDelay();
      
      this.$emit('success', {
        ...paymentData,
        transactionId: this.generateTransactionId(),
        status: 'pending_verification', // Will be verified manually
        message: '  24 '
      });
    },
    async processQRPayment(paymentData) {
      // Simulate QR payment processing
      await this.simulatePaymentDelay();
      
      // In real implementation, check payment status via API
      const success = Math.random() > 0.05; // 95% success rate for demo
      
      if (success) {
        this.$emit('success', {
          ...paymentData,
          transactionId: this.generateTransactionId(),
          status: 'completed'
        });
      } else {
        throw new Error(' ');
      }
    },
    async uploadReceipt(file) {
      // Simulate file upload
      const formData = new FormData();
      formData.append('receipt', file);
      formData.append('invoiceId', this.invoice._id);
      
      // In real implementation, upload to your server
      return `receipt_${Date.now()}_${file.name}`;
    },
    simulatePaymentDelay() {
      return new Promise(resolve => {
        setTimeout(resolve, 2000 + Math.random() * 3000); // 2-5 seconds delay
      });
    },
    generateTransactionId() {
      return 'TXN' + Date.now() + Math.random().toString(36).substr(2, 5).toUpperCase();
    }
  }
};
</script>

