<template>
  <div class="advanced-package-config bg-white shadow rounded-lg p-6">
    <div class="flex items-center justify-between mb-6">
      <h3 class="text-lg font-medium text-gray-900"> Package </h3>
      <button @click="$emit('close')" class="text-gray-400 hover:text-gray-600">
        <font-awesome-icon :icon="['fas', 'times']" class="w-6 h-6" />
      </button>
    </div>

    <!-- Package Selection -->
    <div class="mb-6">
      <label class="block text-sm font-medium text-gray-700 mb-3"> Package</label>
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div v-for="pkg in availablePackages" :key="pkg._id" 
             @click="selectPackage(pkg)"
             :class="[
               'relative p-4 border-2 rounded-lg cursor-pointer transition-all',
               selectedPackage && selectedPackage._id === pkg._id 
                 ? 'border-indigo-600 bg-indigo-50' 
                 : 'border-gray-200 hover:border-gray-300'
             ]">
          
          <!-- Package Header -->
          <div class="flex items-center justify-between mb-3">
            <h3 class="text-lg font-semibold text-gray-900">{{ pkg.name }}</h3>
            <div class="flex items-center">
              <input type="radio" 
                     :checked="selectedPackage && selectedPackage._id === pkg._id"
                     class="text-indigo-600 focus:ring-indigo-500 border-gray-300">
            </div>
          </div>

          <!-- Package Description -->
          <p class="text-sm text-gray-600 mb-3">{{ pkg.description }}</p>

          <!-- Quick Pricing Display -->
          <div class="text-sm text-indigo-600 font-medium">
             {{ getMinPrice(pkg).toLocaleString() }}/
          </div>
        </div>
      </div>
    </div>

    <!-- Advanced Configuration (when package is selected) -->
    <div v-if="selectedPackage" class="space-y-6">
      
      <!-- Billing Plan Selection -->
      <div class="p-4 border rounded-lg bg-gray-50">
        <h4 class="text-sm font-medium text-gray-900 mb-4"></h4>
        
        <div class="space-y-3">
          <div v-for="cycle in selectedPackage.pricing.billingCycles" :key="cycle.type"
               @click="config.billingCycle = cycle.type"
               :class="[
                 'p-3 border rounded-lg cursor-pointer transition-all',
                 config.billingCycle === cycle.type 
                   ? 'border-indigo-600 bg-indigo-50' 
                   : 'border-gray-200 hover:border-gray-300'
               ]">
            
            <div class="flex items-center justify-between">
              <div class="flex items-center">
                <input type="radio" 
                       :checked="config.billingCycle === cycle.type"
                       class="text-indigo-600 focus:ring-indigo-500 border-gray-300 mr-3">
                <div>
                  <p class="font-medium text-gray-900">{{ cycle.label }}</p>
                  <p class="text-sm text-gray-600">{{ cycle.duration }} </p>
                  <p v-if="cycle.discount > 0" class="text-sm text-green-600">
                     {{ cycle.discount }}%
                  </p>
                </div>
              </div>
              <div class="text-right">
                <p class="text-lg font-semibold text-indigo-600">
                  {{ cycle.price.toLocaleString() }}
                </p>
                <p class="text-sm text-gray-500">
                  {{ (cycle.price / (cycle.duration / 30)).toFixed(0) }}/
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Schedule Configuration -->
      <div class="p-4 border rounded-lg bg-gray-50">
        <h4 class="text-sm font-medium text-gray-900 mb-4"></h4>
        
        <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
          <!-- Start Date Configuration -->
          <div class="space-y-4">
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select v-model="config.startDateType" 
                      class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
                <option value="immediate"></option>
                <option value="next_business_day"></option>
                <option value="specific_date"></option>
                <option value="next_month"></option>
                <option value="custom_delay"> ()</option>
              </select>
            </div>

            <!-- Specific Date Selection -->
            <div v-if="config.startDateType === 'specific_date'">
              <FlexibleDateTimePicker
                v-model="config.specificStartDate"
                label=""
                :min-date="today"
                :business-days-only="true"
                :custom-validation="validateStartDate"
                required
              />
            </div>

            <!-- Custom Delay -->
            <div v-if="config.startDateType === 'custom_delay'">
              <label class="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <input v-model="config.delayDays" type="number" min="1" max="365"
                     class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            </div>
          </div>

          <!-- End Date Configuration -->
          <div class="space-y-4">
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <select v-model="config.endDateType" 
                      class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
                <option value="auto_renewal"></option>
                <option value="fixed_duration"></option>
                <option value="specific_end_date"></option>
                <option value="usage_based"></option>
              </select>
            </div>

            <!-- Fixed Duration -->
            <div v-if="config.endDateType === 'fixed_duration'">
              <label class="block text-sm font-medium text-gray-700 mb-2">
                 ()
              </label>
              <input v-model="config.fixedDuration" type="number" min="7" max="3650"
                     class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            </div>

            <!-- Specific End Date -->
            <div v-if="config.endDateType === 'specific_end_date'">
              <FlexibleDateTimePicker
                v-model="config.specificEndDate"
                label=""
                :min-date="calculateMinEndDate()"
                :custom-validation="validateEndDate"
                required
              />
            </div>
          </div>
        </div>
      </div>

      <!-- Advanced Features -->
      <div class="p-4 border rounded-lg bg-gray-50">
        <h4 class="text-sm font-medium text-gray-900 mb-4"></h4>
        
        <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
          <!-- Trial Period -->
          <div class="space-y-4">
            <div>
              <label class="inline-flex items-center">
                <input v-model="config.enableTrial" type="checkbox" 
                       class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                <span class="ml-2 text-sm text-gray-700"></span>
              </label>
            </div>

            <div v-if="config.enableTrial" class="space-y-3">
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-2">
                   ()
                </label>
                <select v-model="config.trialDays" 
                        class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
                  <option value="7">7 </option>
                  <option value="14">14 </option>
                  <option value="30">30 </option>
                  <option value="custom"></option>
                </select>
              </div>

              <div v-if="config.trialDays === 'custom'">
                <input v-model="config.customTrialDays" type="number" min="1" max="365"
                       placeholder=""
                       class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
              </div>

              <div>
                <label class="inline-flex items-center">
                  <input v-model="config.requireCreditCard" type="checkbox" 
                         class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                  <span class="ml-2 text-sm text-gray-700"></span>
                </label>
              </div>
            </div>
          </div>

          <!-- Billing and Payment -->
          <div class="space-y-4">
            <div>
              <label class="inline-flex items-center">
                <input v-model="config.enableProration" type="checkbox" 
                       class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                <span class="ml-2 text-sm text-gray-700"></span>
              </label>
            </div>

            <div>
              <label class="inline-flex items-center">
                <input v-model="config.enableGracePeriod" type="checkbox" 
                       class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                <span class="ml-2 text-sm text-gray-700"></span>
              </label>
            </div>

            <div v-if="config.enableGracePeriod">
              <label class="block text-sm font-medium text-gray-700 mb-2">
                 ()
              </label>
              <select v-model="config.gracePeriodDays" 
                      class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
                <option value="3">3 </option>
                <option value="7">7 </option>
                <option value="15">15 </option>
                <option value="30">30 </option>
              </select>
            </div>

            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2">
                
              </label>
              <div class="space-y-2">
                <label v-for="day in [7, 14, 30]" :key="day" class="inline-flex items-center mr-4">
                  <input v-model="config.reminderDays" :value="day" type="checkbox" 
                         class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                  <span class="ml-2 text-sm text-gray-700">{{ day }} </span>
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Cost Summary -->
      <div class="p-4 bg-indigo-50 rounded-lg border border-indigo-200">
        <h4 class="text-sm font-medium text-indigo-900 mb-4"></h4>
        
        <div class="space-y-2 text-sm">
          <div class="flex justify-between">
            <span class="text-indigo-700">{{ selectedBillingCycle?.label }}:</span>
            <span class="font-medium text-indigo-900">{{ selectedBillingCycle?.price.toLocaleString() }}</span>
          </div>
          
          <div v-if="getTrialDays() > 0" class="flex justify-between">
            <span class="text-indigo-700">:</span>
            <span class="font-medium text-green-600">{{ getTrialDays() }}  ()</span>
          </div>
          
          <div v-if="config.enableProration && prorationAmount" class="flex justify-between">
            <span class="text-indigo-700">:</span>
            <span class="font-medium text-indigo-900">{{ prorationAmount.toLocaleString() }}</span>
          </div>
          
          <div class="border-t border-indigo-200 pt-2 flex justify-between font-semibold">
            <span class="text-indigo-900">:</span>
            <span class="text-indigo-900">{{ totalAmount.toLocaleString() }}</span>
          </div>
          
          <div class="text-xs text-indigo-600">
            <p>: {{ formatStartDate() }}</p>
            <p>: {{ formatEndDate() }}</p>
          </div>
        </div>
      </div>

      <!-- Action Buttons -->
      <div class="flex justify-end space-x-3">
        <button @click="$emit('close')" 
                class="px-4 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50">
          
        </button>
        <button @click="confirmConfiguration" 
                :disabled="!isConfigurationValid"
                class="px-4 py-2 border border-transparent rounded-md text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 disabled:bg-gray-300">
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import FlexibleDateTimePicker from './FlexibleDateTimePicker.vue';

export default {
  name: 'AdvancedPackageConfig',
  components: {
    FlexibleDateTimePicker
  },
  props: {
    collection: {
      type: Object,
      required: true
    },
    hostkey: {
      type: String,
      required: true
    }
  },
  emits: ['close', 'configured'],
  data() {
    return {
      availablePackages: [],
      selectedPackage: null,
      config: {
        billingCycle: 'monthly',
        startDateType: 'immediate',
        specificStartDate: null,
        delayDays: 1,
        endDateType: 'auto_renewal',
        fixedDuration: 30,
        specificEndDate: null,
        enableTrial: false,
        trialDays: '7',
        customTrialDays: 7,
        requireCreditCard: true,
        enableProration: false,
        enableGracePeriod: false,
        gracePeriodDays: '7',
        reminderDays: [7, 30]
      },
      loading: false,
      error: null
    };
  },
  computed: {
    today() {
      return new Date().toISOString().split('T')[0];
    },
    selectedBillingCycle() {
      if (!this.selectedPackage) return null;
      return this.selectedPackage.pricing.billingCycles.find(
        cycle => cycle.type === this.config.billingCycle
      );
    },
    prorationAmount() {
      if (!this.config.enableProration || !this.selectedBillingCycle) return 0;
      
      const startDate = this.getCalculatedStartDate();
      const endOfMonth = new Date(startDate.getFullYear(), startDate.getMonth() + 1, 0);
      const daysRemaining = Math.ceil((endOfMonth - startDate) / (1000 * 60 * 60 * 24));
      const totalDaysInMonth = endOfMonth.getDate();
      
      return Math.round((this.selectedBillingCycle.price / totalDaysInMonth) * daysRemaining);
    },
    totalAmount() {
      if (!this.selectedBillingCycle) return 0;
      
      if (this.getTrialDays() > 0) {
        return 0; // 
      }
      
      return this.config.enableProration ? this.prorationAmount : this.selectedBillingCycle.price;
    },
    isConfigurationValid() {
      if (!this.selectedPackage) return false;
      if (this.config.startDateType === 'specific_date' && !this.config.specificStartDate) return false;
      if (this.config.endDateType === 'specific_end_date' && !this.config.specificEndDate) return false;
      if (this.config.startDateType === 'custom_delay' && !this.config.delayDays) return false;
      return true;
    }
  },
  methods: {
    async loadPackages() {
      try {
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/package/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'find',
            args: [{ status: true }]
          })
        });

        if (response.ok) {
          this.availablePackages = await response.json();
        }
      } catch (error) {
        console.error('Error loading packages:', error);
        this.error = ' Package ';
      }
    },
    selectPackage(pkg) {
      this.selectedPackage = pkg;
      this.config.billingCycle = pkg.pricing.defaultCycle || 'monthly';
    },
    getMinPrice(pkg) {
      return Math.min(...pkg.pricing.billingCycles.map(cycle => cycle.price / (cycle.duration / 30)));
    },
    getTrialDays() {
      if (!this.config.enableTrial) return 0;
      return this.config.trialDays === 'custom' ? this.config.customTrialDays : parseInt(this.config.trialDays);
    },
    getCalculatedStartDate() {
      const today = new Date();
      
      switch (this.config.startDateType) {
        case 'immediate':
          return today;
        case 'next_business_day':
          return this.getNextBusinessDay(today);
        case 'specific_date':
          return this.config.specificStartDate ? new Date(this.config.specificStartDate) : today;
        case 'next_month':
          return new Date(today.getFullYear(), today.getMonth() + 1, 1);
        case 'custom_delay': {
          const delayedDate = new Date(today);
          delayedDate.setDate(delayedDate.getDate() + parseInt(this.config.delayDays));
          return delayedDate;
        }
        default:
          return today;
      }
    },
    getCalculatedEndDate() {
      const startDate = this.getCalculatedStartDate();
      
      switch (this.config.endDateType) {
        case 'auto_renewal': {
          const endDate = new Date(startDate);
          endDate.setDate(endDate.getDate() + this.selectedBillingCycle.duration);
          return endDate;
        }
        case 'fixed_duration': {
          const fixedEndDate = new Date(startDate);
          fixedEndDate.setDate(fixedEndDate.getDate() + parseInt(this.config.fixedDuration));
          return fixedEndDate;
        }
        case 'specific_end_date':
          return this.config.specificEndDate ? new Date(this.config.specificEndDate) : null;
        case 'usage_based':
          return null; // 
        default:
          return null;
      }
    },
    getNextBusinessDay(date) {
      const nextDay = new Date(date);
      nextDay.setDate(nextDay.getDate() + 1);
      
      // Skip weekends
      while (nextDay.getDay() === 0 || nextDay.getDay() === 6) {
        nextDay.setDate(nextDay.getDate() + 1);
      }
      
      return nextDay;
    },
    calculateMinEndDate() {
      if (this.config.startDateType === 'specific_date' && this.config.specificStartDate) {
        const minEndDate = new Date(this.config.specificStartDate);
        minEndDate.setDate(minEndDate.getDate() + 7); //  7 
        return minEndDate.toISOString().split('T')[0];
      }
      
      const today = new Date();
      today.setDate(today.getDate() + 7);
      return today.toISOString().split('T')[0];
    },
    validateStartDate(date) {
      if (!date) return '';
      
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      
      if (date < today) {
        return '';
      }
      
      return null;
    },
    validateEndDate(date) {
      if (!date) return '';
      
      const startDate = this.getCalculatedStartDate();
      if (date <= startDate) {
        return '';
      }
      
      const diffDays = Math.ceil((date - startDate) / (1000 * 60 * 60 * 24));
      if (diffDays < 7) {
        return ' 7 ';
      }
      
      return null;
    },
    formatStartDate() {
      const date = this.getCalculatedStartDate();
      return date.toLocaleDateString('th-TH');
    },
    formatEndDate() {
      const date = this.getCalculatedEndDate();
      return date ? date.toLocaleDateString('th-TH') : '';
    },
    async confirmConfiguration() {
      try {
        this.loading = true;
        
        const startDate = this.getCalculatedStartDate();
        const endDate = this.getCalculatedEndDate();
        
        const configurationData = {
          package: this.selectedPackage,
          configuration: {
            ...this.config,
            calculatedStartDate: startDate.toISOString(),
            calculatedEndDate: endDate ? endDate.toISOString() : null,
            trialDays: this.getTrialDays(),
            totalAmount: this.totalAmount
          },
          collection: this.collection
        };
        
        this.$emit('configured', configurationData);
        
      } catch (error) {
        console.error('Error confirming configuration:', error);
        this.error = '';
      } finally {
        this.loading = false;
      }
    }
  },
  async mounted() {
    await this.loadPackages();
  }
};
</script>

