<template>
  <div class="package-billing-history">
    <!-- Header -->
    <div class="flex items-center justify-between mb-6">
      <div>
        <h3 class="text-lg font-medium text-gray-900"></h3>
        <p class="text-sm text-gray-600"></p>
      </div>
      
      <div class="flex space-x-3">
        <!-- Export Button -->
        <button @click="exportBillingHistory"
                class="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50">
          <font-awesome-icon :icon="['fas', 'download']" class="mr-2" />
          
        </button>
        
        <!-- Filter Button -->
        <button @click="showFilters = !showFilters"
                class="inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50">
          <font-awesome-icon :icon="['fas', 'filter']" class="mr-2" />
          
        </button>
      </div>
    </div>

    <!-- Filters Panel -->
    <div v-if="showFilters" class="mb-6 p-4 bg-gray-50 rounded-lg border">
      <div class="grid grid-cols-1 md:grid-cols-4 gap-4">
        <!-- Date Range -->
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2"></label>
          <select v-model="filters.dateRange" @change="applyFilters"
                  class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            <option value="all"></option>
            <option value="last_7_days">7 </option>
            <option value="last_30_days">30 </option>
            <option value="last_3_months">3 </option>
            <option value="last_year">1 </option>
            <option value="custom"></option>
          </select>
        </div>

        <!-- Status Filter -->
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2"></label>
          <select v-model="filters.status" @change="applyFilters"
                  class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            <option value="all"></option>
            <option value="paid"></option>
            <option value="pending"></option>
            <option value="overdue"></option>
            <option value="failed"></option>
            <option value="refunded"></option>
          </select>
        </div>

        <!-- Package Filter -->
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">Package</label>
          <select v-model="filters.packageId" @change="applyFilters"
                  class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            <option value="all"> Package</option>
            <option v-for="pkg in uniquePackages" :key="pkg._id" :value="pkg._id">
              {{ pkg.name }}
            </option>
          </select>
        </div>

        <!-- Amount Range -->
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2"></label>
          <select v-model="filters.amountRange" @change="applyFilters"
                  class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            <option value="all"></option>
            <option value="0-1000">0 - 1,000 </option>
            <option value="1000-5000">1,000 - 5,000 </option>
            <option value="5000-10000">5,000 - 10,000 </option>
            <option value="10000+"> 10,000 </option>
          </select>
        </div>
      </div>

      <!-- Custom Date Range -->
      <div v-if="filters.dateRange === 'custom'" class="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2"></label>
          <input v-model="filters.customStartDate" type="date" 
                 @change="applyFilters"
                 class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2"></label>
          <input v-model="filters.customEndDate" type="date" 
                 @change="applyFilters"
                 class="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
        </div>
      </div>
    </div>

    <!-- Summary Cards -->
    <div class="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
      <div class="bg-white overflow-hidden shadow rounded-lg">
        <div class="p-5">
          <div class="flex items-center">
            <div class="flex-shrink-0">
              <font-awesome-icon :icon="['fas', 'chart-line']" class="h-6 w-6 text-green-400" />
            </div>
            <div class="ml-5 w-0 flex-1">
              <dl>
                <dt class="text-sm font-medium text-gray-500 truncate"></dt>
                <dd class="text-lg font-medium text-gray-900">{{ summary.totalRevenue.toLocaleString() }}</dd>
              </dl>
            </div>
          </div>
        </div>
      </div>

      <div class="bg-white overflow-hidden shadow rounded-lg">
        <div class="p-5">
          <div class="flex items-center">
            <div class="flex-shrink-0">
              <font-awesome-icon :icon="['fas', 'file-invoice']" class="h-6 w-6 text-blue-400" />
            </div>
            <div class="ml-5 w-0 flex-1">
              <dl>
                <dt class="text-sm font-medium text-gray-500 truncate"></dt>
                <dd class="text-lg font-medium text-gray-900">{{ summary.totalInvoices }}</dd>
              </dl>
            </div>
          </div>
        </div>
      </div>

      <div class="bg-white overflow-hidden shadow rounded-lg">
        <div class="p-5">
          <div class="flex items-center">
            <div class="flex-shrink-0">
              <font-awesome-icon :icon="['fas', 'clock']" class="h-6 w-6 text-yellow-400" />
            </div>
            <div class="ml-5 w-0 flex-1">
              <dl>
                <dt class="text-sm font-medium text-gray-500 truncate"></dt>
                <dd class="text-lg font-medium text-gray-900">{{ summary.pendingAmount.toLocaleString() }}</dd>
              </dl>
            </div>
          </div>
        </div>
      </div>

      <div class="bg-white overflow-hidden shadow rounded-lg">
        <div class="p-5">
          <div class="flex items-center">
            <div class="flex-shrink-0">
              <font-awesome-icon :icon="['fas', 'calendar-alt']" class="h-6 w-6 text-indigo-400" />
            </div>
            <div class="ml-5 w-0 flex-1">
              <dl>
                <dt class="text-sm font-medium text-gray-500 truncate">/</dt>
                <dd class="text-lg font-medium text-gray-900">{{ summary.averageMonthly.toLocaleString() }}</dd>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Billing History Table -->
    <div class="bg-white shadow overflow-hidden sm:rounded-md">
      <div class="px-4 py-5 sm:p-6">
        <!-- Loading State -->
        <div v-if="loading" class="text-center py-8">
          <div class="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-600"></div>
          <p class="mt-2 text-sm text-gray-600">...</p>
        </div>

        <!-- Empty State -->
        <div v-else-if="filteredBillingHistory.length === 0" class="text-center py-8">
          <font-awesome-icon :icon="['fas', 'receipt']" class="h-12 w-12 text-gray-400 mx-auto mb-4" />
          <p class="text-gray-600 mb-4"></p>
        </div>

        <!-- Billing History List -->
        <div v-else class="space-y-4">
          <div v-for="bill in paginatedBillingHistory" :key="bill._id" 
               class="border border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors">
            
            <div class="flex items-center justify-between mb-3">
              <div class="flex items-center">
                <div class="flex-shrink-0">
                  <font-awesome-icon :icon="getStatusIcon(bill.status)" 
                                     :class="getStatusIconClass(bill.status)" />
                </div>
                <div class="ml-3">
                  <p class="text-sm font-medium text-gray-900">
                    Invoice #{{ bill.invoiceNumber || bill._id.slice(-8) }}
                  </p>
                  <p class="text-sm text-gray-600">{{ bill.packageName }}</p>
                </div>
              </div>
              
              <div class="flex items-center space-x-4">
                <div class="text-right">
                  <p class="text-lg font-semibold text-gray-900">{{ bill.amount.toLocaleString() }}</p>
                  <p class="text-sm text-gray-600">{{ bill.billingCycle }}</p>
                </div>
                
                <span :class="[
                  'inline-flex px-2 py-1 rounded-full text-xs font-medium',
                  getStatusClass(bill.status)
                ]">
                  {{ getStatusText(bill.status) }}
                </span>
              </div>
            </div>

            <!-- Bill Details -->
            <div class="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm text-gray-600">
              <div>
                <p><strong>:</strong> {{ formatDate(bill.billingDate) }}</p>
                <p><strong>:</strong> {{ formatDate(bill.dueDate) }}</p>
              </div>
              <div>
                <p><strong>:</strong></p>
                <p>{{ formatDate(bill.servicePeriod.start) }} - {{ formatDate(bill.servicePeriod.end) }}</p>
              </div>
              <div>
                <p><strong>:</strong> {{ bill.paymentMethod || '' }}</p>
                <p v-if="bill.paidDate"><strong>:</strong> {{ formatDate(bill.paidDate) }}</p>
              </div>
            </div>

            <!-- Action Buttons -->
            <div class="mt-4 flex justify-end space-x-2">
              <button @click="viewInvoice(bill)" 
                      class="inline-flex items-center px-3 py-1 border border-gray-300 text-xs font-medium rounded text-gray-700 bg-white hover:bg-gray-50">
                <font-awesome-icon :icon="['fas', 'eye']" class="mr-1" />
                
              </button>
              
              <button @click="downloadInvoice(bill)" 
                      class="inline-flex items-center px-3 py-1 border border-gray-300 text-xs font-medium rounded text-gray-700 bg-white hover:bg-gray-50">
                <font-awesome-icon :icon="['fas', 'download']" class="mr-1" />
                
              </button>
              
              <button v-if="bill.status === 'pending'" @click="payNow(bill)" 
                      class="inline-flex items-center px-3 py-1 border border-transparent text-xs font-medium rounded text-white bg-indigo-600 hover:bg-indigo-700">
                <font-awesome-icon :icon="['fas', 'credit-card']" class="mr-1" />
                
              </button>
            </div>
          </div>
        </div>

        <!-- Pagination -->
        <div v-if="filteredBillingHistory.length > itemsPerPage" class="mt-6 flex items-center justify-between">
          <div class="text-sm text-gray-700">
             {{ startIndex + 1 }}-{{ endIndex }}  {{ filteredBillingHistory.length }} 
          </div>
          <div class="flex space-x-2">
            <button @click="previousPage" :disabled="currentPage === 1"
                    class="px-3 py-1 border rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed">
              
            </button>
            <span class="px-3 py-1 text-sm">{{ currentPage }} / {{ totalPages }}</span>
            <button @click="nextPage" :disabled="currentPage === totalPages"
                    class="px-3 py-1 border rounded text-sm disabled:opacity-50 disabled:cursor-not-allowed">
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Invoice Detail Modal -->
    <div v-if="showInvoiceModal" class="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
      <div class="relative top-20 mx-auto p-5 border w-11/12 md:w-3/4 lg:w-1/2 shadow-lg rounded-md bg-white">
        <InvoiceDetail 
          :invoice="selectedInvoice" 
          @close="showInvoiceModal = false"
          @paid="handleInvoicePaid"
        />
      </div>
    </div>
  </div>
</template>

<script>
import InvoiceDetail from './InvoiceDetail.vue';

export default {
  name: 'PackageBillingHistory',
  components: {
    InvoiceDetail
  },
  props: {
    collection: {
      type: Object,
      required: true
    },
    hostkey: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      billingHistory: [],
      uniquePackages: [],
      loading: false,
      showFilters: false,
      showInvoiceModal: false,
      selectedInvoice: null,
      currentPage: 1,
      itemsPerPage: 10,
      filters: {
        dateRange: 'all',
        status: 'all',
        packageId: 'all',
        amountRange: 'all',
        customStartDate: '',
        customEndDate: ''
      },
      summary: {
        totalRevenue: 0,
        totalInvoices: 0,
        pendingAmount: 0,
        averageMonthly: 0
      }
    };
  },
  computed: {
    filteredBillingHistory() {
      let filtered = [...this.billingHistory];

      // Date filter
      if (this.filters.dateRange !== 'all') {
        const now = new Date();
        let startDate = new Date();

        switch (this.filters.dateRange) {
          case 'last_7_days':
            startDate.setDate(now.getDate() - 7);
            break;
          case 'last_30_days':
            startDate.setDate(now.getDate() - 30);
            break;
          case 'last_3_months':
            startDate.setMonth(now.getMonth() - 3);
            break;
          case 'last_year':
            startDate.setFullYear(now.getFullYear() - 1);
            break;
          case 'custom':
            if (this.filters.customStartDate) {
              startDate = new Date(this.filters.customStartDate);
            }
            break;
        }

        filtered = filtered.filter(bill => {
          const billDate = new Date(bill.billingDate);
          if (this.filters.dateRange === 'custom' && this.filters.customEndDate) {
            const endDate = new Date(this.filters.customEndDate);
            return billDate >= startDate && billDate <= endDate;
          }
          return billDate >= startDate;
        });
      }

      // Status filter
      if (this.filters.status !== 'all') {
        filtered = filtered.filter(bill => bill.status === this.filters.status);
      }

      // Package filter
      if (this.filters.packageId !== 'all') {
        filtered = filtered.filter(bill => bill.packageId === this.filters.packageId);
      }

      // Amount filter
      if (this.filters.amountRange !== 'all') {
        const [min, max] = this.filters.amountRange.includes('+') 
          ? [parseInt(this.filters.amountRange.replace('+', '')), Infinity]
          : this.filters.amountRange.split('-').map(Number);

        filtered = filtered.filter(bill => bill.amount >= min && bill.amount <= max);
      }

      return filtered.sort((a, b) => new Date(b.billingDate) - new Date(a.billingDate));
    },
    paginatedBillingHistory() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredBillingHistory.slice(start, end);
    },
    totalPages() {
      return Math.ceil(this.filteredBillingHistory.length / this.itemsPerPage);
    },
    startIndex() {
      return (this.currentPage - 1) * this.itemsPerPage;
    },
    endIndex() {
      return Math.min(this.startIndex + this.itemsPerPage, this.filteredBillingHistory.length);
    }
  },
  methods: {
    async loadBillingHistory() {
      try {
        this.loading = true;
        
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/billing-history/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'find',
            args: [{ collectionId: this.collection._id }]
          })
        });

        if (response.ok) {
          this.billingHistory = await response.json();
          this.calculateSummary();
          this.extractUniquePackages();
        }
      } catch (error) {
        console.error('Error loading billing history:', error);
      } finally {
        this.loading = false;
      }
    },
    calculateSummary() {
      const paid = this.billingHistory.filter(bill => bill.status === 'paid');
      const pending = this.billingHistory.filter(bill => bill.status === 'pending');
      
      this.summary = {
        totalRevenue: paid.reduce((sum, bill) => sum + bill.amount, 0),
        totalInvoices: this.billingHistory.length,
        pendingAmount: pending.reduce((sum, bill) => sum + bill.amount, 0),
        averageMonthly: paid.length > 0 ? paid.reduce((sum, bill) => sum + bill.amount, 0) / paid.length : 0
      };
    },
    extractUniquePackages() {
      const packageMap = new Map();
      this.billingHistory.forEach(bill => {
        if (!packageMap.has(bill.packageId)) {
          packageMap.set(bill.packageId, {
            _id: bill.packageId,
            name: bill.packageName
          });
        }
      });
      this.uniquePackages = Array.from(packageMap.values());
    },
    applyFilters() {
      this.currentPage = 1; // Reset to first page when filtering
    },
    previousPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    viewInvoice(bill) {
      this.selectedInvoice = bill;
      this.showInvoiceModal = true;
    },
    async downloadInvoice(bill) {
      try {
        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/billing-history/${bill._id}/download`, {
          headers: {
            'client-token-key': this.hostkey
          }
        });

        if (response.ok) {
          const blob = await response.blob();
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.href = url;
          a.download = `invoice-${bill._id}.pdf`;
          document.body.appendChild(a);
          a.click();
          window.URL.revokeObjectURL(url);
          document.body.removeChild(a);
        }
      } catch (error) {
        console.error('Error downloading invoice:', error);
        alert('');
      }
    },
    async payNow(bill) {
      try {
        // Redirect to payment gateway or open payment modal
        const paymentUrl = `https://payment.gateway.com/pay?invoice=${bill._id}&amount=${bill.amount}`;
        window.open(paymentUrl, '_blank');
      } catch (error) {
        console.error('Error initiating payment:', error);
        alert('');
      }
    },
    async exportBillingHistory() {
      try {
        const csvContent = this.generateCSV();
        const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `billing-history-${new Date().toISOString().split('T')[0]}.csv`;
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(a);
      } catch (error) {
        console.error('Error exporting billing history:', error);
        alert('');
      }
    },
    generateCSV() {
      const headers = ['', '', 'Package', '', '', ''];
      const rows = this.filteredBillingHistory.map(bill => [
        this.formatDate(bill.billingDate),
        bill.invoiceNumber || bill._id.slice(-8),
        bill.packageName,
        bill.amount,
        this.getStatusText(bill.status),
        bill.paymentMethod || ''
      ]);

      return [headers, ...rows].map(row => row.join(',')).join('\n');
    },
    handleInvoicePaid(invoice) {
      // Update the billing history when invoice is paid
      const index = this.billingHistory.findIndex(bill => bill._id === invoice._id);
      if (index > -1) {
        this.billingHistory[index].status = 'paid';
        this.billingHistory[index].paidDate = new Date().toISOString();
        this.calculateSummary();
      }
      this.showInvoiceModal = false;
    },
    getStatusIcon(status) {
      const icons = {
        'paid': ['fas', 'check-circle'],
        'pending': ['fas', 'clock'],
        'overdue': ['fas', 'exclamation-triangle'],
        'failed': ['fas', 'times-circle'],
        'refunded': ['fas', 'undo']
      };
      return icons[status] || ['fas', 'question-circle'];
    },
    getStatusIconClass(status) {
      const classes = {
        'paid': 'h-5 w-5 text-green-500',
        'pending': 'h-5 w-5 text-yellow-500',
        'overdue': 'h-5 w-5 text-red-500',
        'failed': 'h-5 w-5 text-red-500',
        'refunded': 'h-5 w-5 text-blue-500'
      };
      return classes[status] || 'h-5 w-5 text-gray-500';
    },
    getStatusClass(status) {
      const classes = {
        'paid': 'bg-green-100 text-green-800',
        'pending': 'bg-yellow-100 text-yellow-800',
        'overdue': 'bg-red-100 text-red-800',
        'failed': 'bg-red-100 text-red-800',
        'refunded': 'bg-blue-100 text-blue-800'
      };
      return classes[status] || 'bg-gray-100 text-gray-800';
    },
    getStatusText(status) {
      const texts = {
        'paid': '',
        'pending': '',
        'overdue': '',
        'failed': '',
        'refunded': ''
      };
      return texts[status] || status;
    },
    formatDate(dateString) {
      if (!dateString) return '';
      return new Date(dateString).toLocaleDateString('th-TH');
    }
  },
  async mounted() {
    await this.loadBillingHistory();
  }
};
</script>

<style scoped>
.package-billing-history {
  max-width: 100%;
  overflow-x: auto;
}

/* Custom scrollbar */
.package-billing-history::-webkit-scrollbar {
  height: 6px;
}

.package-billing-history::-webkit-scrollbar-track {
  background-color: #f3f4f6;
  border-radius: 0.25rem;
}

.package-billing-history::-webkit-scrollbar-thumb {
  background-color: #9ca3af;
  border-radius: 0.25rem;
}

.package-billing-history::-webkit-scrollbar-thumb:hover {
  background-color: #6b7280;
}

/* Animation for cards */
.hover\:bg-gray-50:hover {
  transition: background-color 0.2s ease-in-out;
}

/* Responsive table */
@media (max-width: 768px) {
  .grid-cols-1.md\:grid-cols-3 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  
  .grid-cols-1.md\:grid-cols-4 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}
</style>
