<template>
  <div class="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50" v-if="isOpen">
    <div class="relative top-20 mx-auto p-5 border w-11/12 md:w-3/4 lg:w-1/2 shadow-lg rounded-md bg-white">
      <div class="mt-3">
        <!-- Header -->
        <div class="flex items-center justify-between pb-4 border-b">
          <h3 class="text-lg font-medium text-gray-900">
             Package  Collection
          </h3>
          <button @click="closeModal" class="text-gray-400 hover:text-gray-600">
            <font-awesome-icon :icon="['fas', 'times']" class="w-6 h-6" />
          </button>
        </div>

        <!-- Error/Success Messages -->
        <div v-if="error" class="mt-4 rounded-md bg-red-50 p-4">
          <div class="flex">
            <font-awesome-icon :icon="['fas', 'exclamation-circle']" class="h-5 w-5 text-red-400 mr-3" />
            <div>
              <h3 class="text-sm font-medium text-red-800"></h3>
              <p class="text-sm text-red-700">{{ error }}</p>
            </div>
          </div>
        </div>

        <div v-if="success" class="mt-4 rounded-md bg-green-50 p-4">
          <div class="flex">
            <font-awesome-icon :icon="['fas', 'check-circle']" class="h-5 w-5 text-green-400 mr-3" />
            <div>
              <h3 class="text-sm font-medium text-green-800"></h3>
              <p class="text-sm text-green-700"> Package </p>
            </div>
          </div>
        </div>

        <!-- Collection Info -->
        <div v-if="collection" class="mt-6 p-4 bg-gray-50 rounded-lg">
          <h4 class="text-sm font-medium text-gray-900 mb-2">Collection</h4>
          <div class="flex items-center">
            <div class="flex-shrink-0 h-10 w-10">
              <img :src="collection.siteLogo || 'https://via.placeholder.com/40'" 
                   class="h-10 w-10 rounded-full object-cover" alt="">
            </div>
            <div class="ml-4">
              <p class="text-sm font-medium text-gray-900">{{ collection.siteName }}</p>
              <p class="text-sm text-gray-500">{{ collection.hostname }}</p>
            </div>
          </div>
        </div>

        <!-- Package Selection -->
        <div class="mt-6">
          <label class="block text-sm font-medium text-gray-700 mb-3"> Package</label>
          <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div v-for="pkg in availablePackages" :key="pkg._id" 
                 @click="selectedPackage = pkg"
                 :class="[
                   'relative p-4 border-2 rounded-lg cursor-pointer transition-all',
                   selectedPackage && selectedPackage._id === pkg._id 
                     ? 'border-indigo-600 bg-indigo-50' 
                     : 'border-gray-200 hover:border-gray-300'
                 ]">
              
              <!-- Package Header -->
              <div class="flex items-center justify-between mb-3">
                <h3 class="text-lg font-semibold text-gray-900">{{ pkg.name }}</h3>
                <div class="flex items-center">
                  <input type="radio" 
                         :checked="selectedPackage && selectedPackage._id === pkg._id"
                         @change="selectedPackage = pkg"
                         class="text-indigo-600 focus:ring-indigo-500 border-gray-300">
                </div>
              </div>

              <!-- Package Description -->
              <p class="text-sm text-gray-600 mb-3">{{ pkg.description }}</p>

              <!-- Billing Options -->
              <div class="space-y-2">
                <div v-for="cycle in (pkg.pricing?.billingCycles || [])" :key="cycle.type" 
                     class="flex items-center justify-between p-2 bg-white rounded border">
                  <div class="flex items-center">
                    <span class="text-sm font-medium text-gray-900">{{ cycle.label }}</span>
                    <span v-if="cycle.discount > 0" class="ml-2 text-xs text-green-600 bg-green-100 px-2 py-1 rounded">
                       {{ cycle.discount }}%
                    </span>
                  </div>
                  <span class="text-sm font-semibold text-indigo-600">
                    {{ cycle.price.toLocaleString() }}
                  </span>
                </div>
              </div>

              <!-- Package Limits -->
              <div class="mt-3 grid grid-cols-2 gap-2 text-xs">
                <div class="flex items-center">
                  <font-awesome-icon :icon="['fas', 'database']" class="text-indigo-600 mr-1" />
                  <span>{{ pkg.limits.maxAssets }} Assets</span>
                </div>
                <div class="flex items-center">
                  <font-awesome-icon :icon="['fas', 'users']" class="text-indigo-600 mr-1" />
                  <span>{{ pkg.limits.maxUsers }} Users</span>
                </div>
                <div class="flex items-center">
                  <font-awesome-icon :icon="['fas', 'hdd']" class="text-indigo-600 mr-1" />
                  <span>{{ pkg.limits.storageGB }} GB</span>
                </div>
                <div class="flex items-center">
                  <font-awesome-icon :icon="['fas', 'tachometer-alt']" class="text-indigo-600 mr-1" />
                  <span>{{ pkg.limits.bandwidth }} GB/mo</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Package Assignment Configuration -->
        <div v-if="selectedPackage" class="mt-6 p-4 border rounded-lg bg-gray-50">
          <h4 class="text-sm font-medium text-gray-900 mb-4"> Package</h4>
          
          <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
            <!-- Billing Cycle Selection -->
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2"></label>
              <select v-model="assignmentConfig.billingCycle" 
                      class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
                <option v-for="cycle in (selectedPackage?.pricing?.billingCycles || [])" :key="cycle.type" :value="cycle.type">
                  {{ cycle.label }} - {{ cycle.price.toLocaleString() }}
                  <span v-if="cycle.discount > 0">( {{ cycle.discount }}%)</span>
                </option>
              </select>
            </div>

            <!-- Start Date -->
            <div>
              <FlexibleDateTimePicker
                v-model="assignmentConfig.startDate"
                label=""
                placeholder=" Package"
                :min-date="today"
                :show-quick-dates="true"
                :business-days-only="true"
                :custom-validation="validateStartDate"
                required
              />
            </div>

            <!-- Auto Renewal -->
            <div class="md:col-span-2">
              <label class="inline-flex items-center">
                <input v-model="assignmentConfig.autoRenewal" type="checkbox" 
                       class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                <span class="ml-2 text-sm text-gray-700"></span>
              </label>
            </div>

            <!-- Custom End Date (if not auto renewal) -->
            <div v-if="!assignmentConfig.autoRenewal" class="md:col-span-2">
              <FlexibleDateTimePicker
                v-model="assignmentConfig.customEndDate"
                label=" ()"
                placeholder=" Package"
                :min-date="assignmentConfig.startDate"
                :show-quick-dates="true"
                :custom-validation="validateEndDate"
                required
              />
            </div>

            <!-- Trial Period -->
            <div>
              <label class="block text-sm font-medium text-gray-700 mb-2"> ()</label>
              <input v-model="assignmentConfig.trialDays" type="number" min="0" max="365"
                     class="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm">
            </div>

            <!-- Proration -->
            <div>
              <label class="inline-flex items-center">
                <input v-model="assignmentConfig.proration" type="checkbox" 
                       class="rounded border-gray-300 text-indigo-600 shadow-sm focus:border-indigo-500 focus:ring-indigo-500">
                <span class="ml-2 text-sm text-gray-700"></span>
              </label>
            </div>
          </div>

          <!-- Price Summary -->
          <div class="mt-4 p-3 bg-indigo-50 rounded-lg">
            <h5 class="text-sm font-medium text-indigo-900 mb-2"></h5>
            <div class="space-y-1 text-sm">
              <div class="flex justify-between">
                <span class="text-indigo-700">{{ selectedBillingCycle?.label }}:</span>
                <span class="font-medium text-indigo-900">{{ selectedBillingCycle?.price.toLocaleString() }}</span>
              </div>
              <div v-if="assignmentConfig.trialDays > 0" class="flex justify-between">
                <span class="text-indigo-700">:</span>
                <span class="font-medium text-green-600">{{ assignmentConfig.trialDays }}  ()</span>
              </div>
              <div v-if="prorationAmount" class="flex justify-between">
                <span class="text-indigo-700">:</span>
                <span class="font-medium text-indigo-900">{{ prorationAmount.toLocaleString() }}</span>
              </div>
              <div class="border-t border-indigo-200 pt-1 flex justify-between font-semibold">
                <span class="text-indigo-900">:</span>
                <span class="text-indigo-900">{{ totalAmount.toLocaleString() }}</span>
              </div>
            </div>
          </div>
        </div>

        <!-- Action Buttons -->
        <div class="mt-6 flex justify-end space-x-3">
          <button @click="closeModal" 
                  class="px-4 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50">
            
          </button>
          <button @click="assignPackage" 
                  :disabled="!selectedPackage || loading"
                  class="px-4 py-2 border border-transparent rounded-md text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 disabled:bg-gray-300">
            <span v-if="loading">...</span>
            <span v-else> Package</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FlexibleDateTimePicker from './FlexibleDateTimePicker.vue';

export default {
  name: 'PackageAssignmentModal',
  components: {
    FlexibleDateTimePicker
  },
  props: {
    isOpen: {
      type: Boolean,
      default: false
    },
    collection: {
      type: Object,
      default: null
    },
    hostkey: {
      type: String,
      required: true
    }
  },
  emits: ['close', 'assigned'],
  data() {
    return {
      availablePackages: [],
      selectedPackage: null,
      assignmentConfig: {
        billingCycle: 'monthly',
        startDate: this.today,
        autoRenewal: true,
        customEndDate: null,
        trialDays: 0,
        proration: false
      },
      loading: false,
      error: null,
      success: false
    };
  },
  watch: {
    isOpen(newVal) {
      if (newVal) {
        this.loadPackages();
      }
    },
    selectedPackage(newPackage) {
      if (newPackage?.pricing?.billingCycles?.length > 0) {
        // Set default billing cycle to the first available option
        this.assignmentConfig.billingCycle = newPackage.pricing.billingCycles[0].type;
      }
    }
  },
  computed: {
    today() {
      return new Date().toISOString().split('T')[0];
    },
    selectedBillingCycle() {
      if (!this.selectedPackage?.pricing?.billingCycles) return null;
      return this.selectedPackage.pricing.billingCycles.find(
        cycle => cycle.type === this.assignmentConfig.billingCycle
      );
    },
    prorationAmount() {
      if (!this.assignmentConfig.proration || !this.selectedBillingCycle) return 0;
      
      const startDate = new Date(this.assignmentConfig.startDate);
      const endOfMonth = new Date(startDate.getFullYear(), startDate.getMonth() + 1, 0);
      const daysRemaining = Math.ceil((endOfMonth - startDate) / (1000 * 60 * 60 * 24));
      const totalDaysInMonth = endOfMonth.getDate();
      
      return Math.round((this.selectedBillingCycle.price / totalDaysInMonth) * daysRemaining);
    },
    totalAmount() {
      if (!this.selectedBillingCycle) return 0;
      
      if (this.assignmentConfig.trialDays > 0) {
        return 0; // 
      }
      
      return this.assignmentConfig.proration ? this.prorationAmount : this.selectedBillingCycle.price;
    }
  },
  methods: {
    validateStartDate(date) {
      if (!date) return '';
      
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      
      if (date < today) {
        return '';
      }
      
      // Check if it's a weekend and business days only
      const dayOfWeek = date.getDay();
      if (dayOfWeek === 0 || dayOfWeek === 6) {
        return '- ';
      }
      
      return null;
    },
    validateEndDate(date) {
      if (!date) return '';
      
      if (!this.assignmentConfig.startDate) {
        return '';
      }
      
      const startDate = new Date(this.assignmentConfig.startDate);
      if (date <= startDate) {
        return '';
      }
      
      // Check minimum package duration (e.g., at least 7 days)
      const diffDays = Math.ceil((date - startDate) / (1000 * 60 * 60 * 24));
      if (diffDays < 7) {
        return ' 7 ';
      }
      
      return null;
    },
    async loadPackages() {
      try {
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/package/query', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            method: 'find',
            args: [{ status: true }]
          })
        });

        if (response.ok) {
          const packages = await response.json();
          // Ensure each package has proper pricing structure
          this.availablePackages = packages.map(pkg => ({
            ...pkg,
            pricing: pkg.pricing || {
              billingCycles: [
                {
                  type: 'monthly',
                  label: '',
                  price: pkg.price || 0,
                  duration: 30,
                  discount: 0
                }
              ]
            }
          }));
        }
      } catch (error) {
        console.error('Error loading packages:', error);
        this.error = ' Package ';
      }
    },
    async assignPackage() {
      try {
        this.loading = true;
        this.error = null;

        if (!this.selectedPackage) {
          this.error = ' Package';
          return;
        }

        if (!this.assignmentConfig.startDate) {
          this.error = '';
          return;
        }

        // Calculate end date
        let endDate;
        if (this.assignmentConfig.autoRenewal) {
          // For auto renewal, calculate based on billing cycle
          const startDate = new Date(this.assignmentConfig.startDate);
          endDate = new Date(startDate);
          const duration = this.selectedBillingCycle?.duration || 30; // Default to 30 days if not specified
          endDate.setDate(endDate.getDate() + duration);
        } else if (this.assignmentConfig.customEndDate) {
          endDate = new Date(this.assignmentConfig.customEndDate);
        } else {
          this.error = '';
          return;
        }

        // Create package assignment
        const assignmentData = {
          collectionId: this.collection._id,
          packageId: this.selectedPackage._id,
          billingCycle: this.assignmentConfig.billingCycle,
          startDate: this.assignmentConfig.startDate,
          endDate: endDate.toISOString().split('T')[0],
          autoRenewal: this.assignmentConfig.autoRenewal,
          trialDays: this.assignmentConfig.trialDays,
          proration: this.assignmentConfig.proration,
          amount: this.totalAmount,
          status: this.assignmentConfig.trialDays > 0 ? 'trial' : 'active',
          createdAt: new Date()
        };

        // Save assignment
        const response = await fetch('https://gateway.cloudrestfulapi.com/api/package-assignment', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            data: assignmentData
          })
        });

        if (response.ok) {
          // Update collection with package assignment
          await this.updateCollectionPackage();
          
          this.success = true;
          setTimeout(() => {
            this.$emit('assigned', {
              package: this.selectedPackage,
              assignment: assignmentData
            });
            this.closeModal();
          }, 1500);
        } else {
          const errorData = await response.json();
          this.error = errorData.message || ' Package';
        }

      } catch (error) {
        console.error('Error assigning package:', error);
        this.error = '';
      } finally {
        this.loading = false;
      }
    },
    async updateCollectionPackage() {
      try {
        await fetch(`https://gateway.cloudrestfulapi.com/api/hostname/${this.collection._id}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            data: {
              packageId: this.selectedPackage._id,
              packageAssignedAt: new Date()
            }
          })
        });
      } catch (error) {
        console.error('Error updating collection package:', error);
      }
    },
    closeModal() {
      this.selectedPackage = null;
      this.error = null;
      this.success = false;
      this.assignmentConfig = {
        billingCycle: 'monthly',
        startDate: this.today,
        autoRenewal: true,
        customEndDate: null,
        trialDays: 0,
        proration: false
      };
      this.$emit('close');
    }
  },
  async mounted() {
    if (this.isOpen) {
      await this.loadPackages();
    }
  }
};
</script>

<style scoped>
.modal-overlay {
  backdrop-filter: blur(4px);
}
</style>
