<script>
import PaymentProcessor from './PaymentProcessor.vue';

export default {
  name: 'InvoiceDetail',
  components: {
    PaymentProcessor
  },
  props: {
    invoice: {
      type: Object,
      required: true
    },
    collection: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['close', 'paid'],
  data() {
    return {
      showPaymentModal: false,
      companyInfo: {
        name: '  ',
        address: '123 ',
        city: '',
        postalCode: '10500',
        taxId: '0123456789012'
      }
    };
  },
  methods: {
    payInvoice() {
      this.showPaymentModal = true;
    },
    async downloadPDF() {
      try {
        // Generate PDF using browser's print functionality
        window.print();
      } catch (error) {
        console.error('Error generating PDF:', error);
        alert(' PDF ');
      }
    },
    calculateSubtotal() {
      let subtotal = this.invoice.amount;
      
      if (this.invoice.additionalCharges?.length) {
        subtotal += this.invoice.additionalCharges.reduce((sum, charge) => 
          sum + (charge.quantity * charge.unitPrice), 0
        );
      }
      
      return subtotal;
    },
    getFinalTotal() {
      let total = this.calculateSubtotal();
      
      if (this.invoice.discount) {
        total -= this.invoice.discount;
      }
      
      if (this.invoice.tax) {
        total += this.invoice.tax;
      }
      
      return total;
    },
    calculateDuration() {
      if (!this.invoice.servicePeriod?.start || !this.invoice.servicePeriod?.end) {
        return 0;
      }
      
      const start = new Date(this.invoice.servicePeriod.start);
      const end = new Date(this.invoice.servicePeriod.end);
      const diffTime = Math.abs(end - start);
      return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    },
    getBillingCycleDescription() {
      const descriptions = {
        'monthly': '',
        'quarterly': '',
        'yearly': '',
        'weekly': ''
      };
      return descriptions[this.invoice.billingCycle] || this.invoice.billingCycle;
    },
    getStatusClass(status) {
      const classes = {
        'paid': 'bg-green-100 text-green-800',
        'pending': 'bg-yellow-100 text-yellow-800',
        'overdue': 'bg-red-100 text-red-800',
        'failed': 'bg-red-100 text-red-800',
        'refunded': 'bg-blue-100 text-blue-800'
      };
      return classes[status] || 'bg-gray-100 text-gray-800';
    },
    getStatusText(status) {
      const texts = {
        'paid': '',
        'pending': '',
        'overdue': '',
        'failed': '',
        'refunded': ''
      };
      return texts[status] || status;
    },
    formatDate(dateString) {
      if (!dateString) return '';
      return new Date(dateString).toLocaleDateString('th-TH', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      });
    },
    handlePaymentSuccess(paymentData) {
      this.showPaymentModal = false;
      this.$emit('paid', {
        ...this.invoice,
        status: 'paid',
        paidDate: new Date().toISOString(),
        paymentMethod: paymentData.method,
        transactionId: paymentData.transactionId
      });
      
      // Show success message
      alert('!');
    },
    handlePaymentError(error) {
      console.error('Payment error:', error);
      alert(' ');
    }
  }
};
</script>

