<template>
  <div class="package-management min-h-screen bg-gray-50">
    <!-- Header -->
    <div class="bg-white shadow-sm">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div class="flex justify-between items-center py-6">
          <div>
            <h1 class="text-2xl font-bold text-gray-900">Package Management</h1>
            <p class="mt-1 text-sm text-gray-500"> Package  Subscription  Collection</p>
          </div>
          <div class="flex space-x-3">
            <button
              @click="refreshData"
              :disabled="loading"
              class="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              <font-awesome-icon 
                :icon="['fas', 'sync-alt']" 
                :class="['mr-2 h-4 w-4', { 'animate-spin': loading }]" 
              />
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Main Content -->
    <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div class="grid grid-cols-1 lg:grid-cols-3 gap-8">
        
        <!-- Left Column: Collection Info & Package Subscription -->
        <div class="lg:col-span-2 space-y-8">
          <!-- Collection Info Card -->
          <div class="bg-white rounded-lg shadow p-6">
            <h2 class="text-lg font-medium text-gray-900 mb-4"> Collection</h2>
            <div v-if="collection" class="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label class="block text-sm font-medium text-gray-700"> Collection</label>
                <p class="mt-1 text-sm text-gray-900">{{ collection.siteName || 'N/A' }}</p>
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700"></label>
                <p class="mt-1 text-sm text-gray-900">{{ collection.siteType || 'N/A' }}</p>
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700"></label>
                <span :class="getStatusClass(collection.status)">
                  {{ collection.status ? '' : '' }}
                </span>
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700"></label>
                <p class="mt-1 text-sm text-gray-900">{{ formatDate(collection.created_at) }}</p>
              </div>
            </div>
          </div>

          <!-- Package Subscription Manager -->
          <PackageSubscriptionManager
            v-if="collection"
            :collection="collection"
            :hostkey="hostkey"
            @refresh="refreshData"
          />
        </div>

        <!-- Right Column: Usage Monitor & Quick Actions -->
        <div class="space-y-8">
          <!-- Package Limit Monitor -->
          <PackageLimitMonitor
            v-if="collection && usage"
            :collection="collection"
            :usage="usage"
            @upgrade-package="showUpgradeModal = true"
            @view-packages="showPackageSelection = true"
            @refresh-usage="refreshUsageData"
          />

          <!-- Quick Actions -->
          <div class="bg-white rounded-lg shadow p-6">
            <h3 class="text-lg font-medium text-gray-900 mb-4"></h3>
            <div class="space-y-3">
              <button
                @click="showBillingHistory = true"
                class="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                <font-awesome-icon :icon="['fas', 'receipt']" class="mr-2 h-4 w-4" />
                
              </button>
              
              <button
                @click="showAdvancedConfig = true"
                class="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                <font-awesome-icon :icon="['fas', 'cog']" class="mr-2 h-4 w-4" />
                
              </button>

              <button
                @click="showPackageAssignment = true"
                class="w-full flex items-center justify-center px-4 py-2 bg-indigo-600 text-white rounded-md shadow-sm text-sm font-medium hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                <font-awesome-icon :icon="['fas', 'plus']" class="mr-2 h-4 w-4" />
                 Package 
              </button>
            </div>
          </div>

          <!-- Recent Activity -->
          <div class="bg-white rounded-lg shadow p-6">
            <h3 class="text-lg font-medium text-gray-900 mb-4"></h3>
            <div class="space-y-3">
              <div v-if="recentActivities.length === 0" class="text-sm text-gray-500 text-center py-4">
                
              </div>
              <div 
                v-for="activity in recentActivities" 
                :key="activity.id"
                class="flex items-start space-x-3 text-sm"
              >
                <div class="flex-shrink-0">
                  <div class="w-2 h-2 bg-indigo-500 rounded-full mt-2"></div>
                </div>
                <div class="min-w-0 flex-1">
                  <p class="text-gray-900">{{ activity.message }}</p>
                  <p class="text-gray-500">{{ formatDate(activity.created_at) }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Modals -->
    <!-- Package Assignment Modal -->
    <PackageAssignmentModal
      :isOpen="showPackageAssignment"
      :collection="collection"
      :hostkey="hostkey"
      @close="showPackageAssignment = false"
      @assigned="handlePackageAssigned"
    />

    <!-- Billing History Modal -->
    <div v-if="showBillingHistory" class="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
      <div class="relative top-4 mx-auto p-5 border w-11/12 md:w-5/6 lg:w-4/5 shadow-lg rounded-md bg-white max-h-screen overflow-y-auto">
        <div class="flex items-center justify-between pb-4 border-b">
          <h3 class="text-lg font-medium text-gray-900"></h3>
          <button @click="showBillingHistory = false" class="text-gray-400 hover:text-gray-600">
            <font-awesome-icon :icon="['fas', 'times']" class="h-6 w-6" />
          </button>
        </div>
        <div class="mt-4">
          <PackageBillingHistory
            :collection="collection"
            :hostkey="hostkey"
            @close="showBillingHistory = false"
          />
        </div>
      </div>
    </div>

    <!-- Advanced Configuration Modal -->
    <div v-if="showAdvancedConfig" class="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
      <div class="relative top-4 mx-auto p-5 border w-11/12 md:w-5/6 lg:w-4/5 shadow-lg rounded-md bg-white max-h-screen overflow-y-auto">
        <div class="flex items-center justify-between pb-4 border-b">
          <h3 class="text-lg font-medium text-gray-900"></h3>
          <button @click="showAdvancedConfig = false" class="text-gray-400 hover:text-gray-600">
            <font-awesome-icon :icon="['fas', 'times']" class="h-6 w-6" />
          </button>
        </div>
        <div class="mt-4">
          <AdvancedPackageConfig
            :collection="collection"
            :hostkey="hostkey"
            @close="showAdvancedConfig = false"
            @configured="handleAdvancedConfigured"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PackageSubscriptionManager from './PackageSubscriptionManager.vue';
import PackageLimitMonitor from './PackageLimitMonitor.vue';
import PackageAssignmentModal from './PackageAssignmentModal.vue';
import PackageBillingHistory from './PackageBillingHistory.vue';
import AdvancedPackageConfig from './AdvancedPackageConfig.vue';

export default {
  name: 'PackageManagement',
  components: {
    PackageSubscriptionManager,
    PackageLimitMonitor,
    PackageAssignmentModal,
    PackageBillingHistory,
    AdvancedPackageConfig
  },
  props: {
    collectionId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      hostkey: this.$Key,
      collection: null,
      usage: {
        assetsCount: 0,
        usersCount: 0,
        storageGB: 0,
        bandwidthGB: 0,
        apiRequestsCount: 0
      },
      recentActivities: [],
      loading: false,
      showPackageAssignment: false,
      showBillingHistory: false,
      showAdvancedConfig: false,
      showUpgradeModal: false,
      showPackageSelection: false
    };
  },
  async mounted() {
    await this.loadData();
  },
  methods: {
    async loadData() {
      this.loading = true;
      try {
        await Promise.all([
          this.loadCollection(),
          this.loadUsageData(),
          this.loadRecentActivities()
        ]);
      } catch (error) {
        console.error('Error loading package management data:', error);
      } finally {
        this.loading = false;
      }
    },

    async loadCollection() {
      try {
        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/hostname/${this.collectionId}`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          }
        });

        if (response.ok) {
          this.collection = await response.json();
        }
      } catch (error) {
        console.error('Error loading collection:', error);
      }
    },

    async loadUsageData() {
      try {
        // Simulate loading usage data - replace with actual API
        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/usage/${this.collectionId}`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          }
        });

        if (response.ok) {
          this.usage = await response.json();
        } else {
          // Fallback with default values
          this.usage = {
            assetsCount: 0,
            usersCount: 0,
            storageGB: 0,
            bandwidthGB: 0,
            apiRequestsCount: 0
          };
        }
      } catch (error) {
        console.error('Error loading usage data:', error);
        // Set default values on error
        this.usage = {
          assetsCount: 0,
          usersCount: 0,
          storageGB: 0,
          bandwidthGB: 0,
          apiRequestsCount: 0
        };
      }
    },

    async loadRecentActivities() {
      try {
        // Simulate loading recent activities - replace with actual API
        this.recentActivities = [
          {
            id: 1,
            message: 'Package upgraded to Professional',
            created_at: new Date().toISOString()
          },
          {
            id: 2,
            message: 'Payment processed successfully',
            created_at: new Date(Date.now() - 86400000).toISOString()
          }
        ];
      } catch (error) {
        console.error('Error loading recent activities:', error);
        this.recentActivities = [];
      }
    },

    async refreshData() {
      await this.loadData();
    },

    async refreshUsageData() {
      await this.loadUsageData();
    },

    handlePackageAssigned(data) {
      this.showPackageAssignment = false;
      this.refreshData();
      
      // Add to recent activities
      this.recentActivities.unshift({
        id: Date.now(),
        message: `Package "${data.packageName}" assigned successfully`,
        created_at: new Date().toISOString()
      });
    },

    handleAdvancedConfigured() {
      this.showAdvancedConfig = false;
      this.refreshData();
      
      // Add to recent activities
      this.recentActivities.unshift({
        id: Date.now(),
        message: 'Advanced configuration updated',
        created_at: new Date().toISOString()
      });
    },

    getStatusClass(status) {
      return status
        ? 'inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800'
        : 'inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800';
    },

    formatDate(dateString) {
      if (!dateString) return 'N/A';
      return new Date(dateString).toLocaleDateString('th-TH', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  }
};
</script>

<style scoped>
